<template>
    <div id="seventy-percent">
      <div class="container">
        <div class="row">
          <div class="col-md-6 mx-auto">
            <div class="card card-body text-center my-5">
              <h1 class="heading display-5 pb-3">70% Rule Calculator</h1>
              <form class="seventy-rule">
                  <p class="required">* All fields are required</p>
                <div class="form-group">
                  <div class="input-group">
                    <div class="input-group-prepend">
                      <div class="input-group-text">ARV</div>
                    </div>
                    <input 
                      v-model="arv" 
                      type="number" 
                      class="form-control"
                      id="arv"
                      @blur="$v.arv.$touch()">
                    <p v-if="!$v.arv.min" class="warning">Please enter an ARV amount greater than 0.</p>
                  </div>
                  <div class="input-group">
                    <div class="input-group-prepend">
                      <div class="input-group-text">Rehab Cost</div>
                    </div>
                    <input 
                      v-model="rehab" 
                      type="number" 
                      class="form-control"
                      id="rehab"
                      @blur="$v.rehab.$touch()">
                    <p v-if="!$v.rehab.min" class="warning">Please enter a rehab amount greater than 0.</p>
                  </div>
                </div>
                <div class="form-group">
                  <button 
                    @click="calculateResluts" 
                    class="btn btn-success btn-block" type="button" 
                    value="send"
                    :disabled="$v.$invalid">
                    Calculate</button>
                </div>
                <hr>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">Purchase Amount</div>
                  </div>
                  <input v-model="purchase" type="text" class="form-control" disabled>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>

  <script>
    import {required, minValue} from 'vuelidate/lib/validators'

    export default {
      data() {
        return {
          arv: '',
          rehab: '',
          purchase: '',
          arvPercent: '',
          purchasePrice: '',
          errorDiv: '',
          card: '',
          heading: '',
        }
      },

      validations: {
        arv: {
          required,
          min: minValue(1)
        },
        rehab: {
          required,
          min: minValue(1)
        }
      },

      methods: {
        calculateResluts(e) {
          this.arvPercent = parseFloat(this.arv) * 0.70;
          this.purchasePrice = this.arvPercent - parseFloat(this.rehab)
          this.purchase = this.purchasePrice.toLocaleString(undefined, {style: "currency", currency: "USD"});

          
        }
      }
    }
  </script>

  <style scoped>
    .warning, .required {
      color: red;
      font-size: 12px;
}
  </style>


