<template>
  <div class="caprate-calc mb-5">
    <div class="container">
      <div class="row">
        <div class="col-md-6 mx-auto">
          <div class="card card-body text-center mt-5">
            <h1 class="heading display-5 pb-3">Cap Rate Calculator</h1>
            <form class="cap-form">
                <p class="required">* All fields are required</p>
              <div class="form-group">
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">$</div>
                  </div>
                  <input 
                    v-model="amount" 
                    type="number" 
                    class="form-control" 
                    id="amount"
                    @blur="$v.amount.$touch()" 
                    placeholder="Purchase Price">
                  <p v-if="!$v.amount.min" class="warning">Please enter a purchase price greater than 0.</p>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">$</div>
                  </div>
                  <input 
                    v-model="rent" 
                    type="number" 
                    class="form-control" 
                    id="rent" 
                    @blur="$v.rent.$touch()" 
                    placeholder="Montly Rental Income">
                  <p v-if="!$v.rent.min" class="warning">Please enter a rental amount greater than 0.</p>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">$</div>
                  </div>
                  <input 
                    v-model="tax" 
                    type="number" 
                    class="form-control" 
                    id="tax" 
                    @blur="$v.tax.$touch()" 
                    placeholder="Montly Taxes">
                  <p v-if="!$v.tax.min" class="warning">Please enter a tax amount greater than 0.</p>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">$</div>
                  </div>
                  <input 
                    v-model="insurance" 
                    type="number" 
                    class="form-control" 
                    id="insurance"
                    @blur="$v.insurance.$touch()"  
                    placeholder="Montly Insurance">
                  <p v-if="!$v.insurance.min" class="warning">Please enter an insurance amount greater than 0.</p>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">%</div>
                  </div>
                  <input 
                    v-model="vacancy" 
                    type="number" 
                    class="form-control" 
                    id="vacancy" 
                    @blur="$v.vacancy.$touch()" 
                    placeholder="Vacancy Rate">
                  <p v-if="!$v.vacancy.between" class="warning">Please enter a vacancy amount between 0 and 100.</p>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">%</div>
                  </div>
                  <input 
                    v-model="repairs" 
                    type="number" 
                    class="form-control" 
                    id="repairs"
                    @blur="$v.repairs.$touch()"  
                    placeholder="Repairs">
                  <p v-if="!$v.repairs.between" class="warning">Please enter a repair amount between 0 and 100.</p>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">%</div>
                  </div>
                  <input 
                    v-model="pm" 
                    type="number" 
                    class="form-control" 
                    id="pm"
                    @blur="$v.pm.$touch()"  
                    placeholder="Property Management Fee">
                  <p v-if="!$v.pm.between" class="warning">Please enter a PM fee between 0 and 100.</p>
                </div>
              </div>
              <div class="form-group">
                <button 
                  @click="calculateResluts" 
                  class="btn btn-success btn-block" type="button" 
                  value="send"
                  :disabled="$v.$invalid">
                  Calculate</button>
              </div>
              <hr>
              <div id="caprate" class="input-group">
                <div class="input-group-prepend">
                  <div class="input-group-text">Cap Rate</div>
                </div>
                <input v-model="cap" type="text" class="form-control" id="cap" disabled>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {required, minValue, between} from 'vuelidate/lib/validators'

  export default {
    data() {
      return {
        amount: '',
        rent: '',
        tax: '',
        insurance: '',
        vacancy: '',
        repairs: '',
        pm: '',
        cap: '',
        vacancyAmount: '',
        repairAmount: '',
        propMangAmount: '',
        totalExpenses: '',
        cashFlow: '',
        yearlyCashFlow: ''
      }
    },

    validations: {
      amount: {
        required,
        min: minValue(1)
      },
      rent: {
        required,
        min: minValue(1)
      },
      tax: {
        required,
        min: minValue(1)
      },
      insurance: {
        required,
        min: minValue(1)
      },
      vacancy: {
        required,
        between: between(0, 100)
      },
      repairs: {
        required,
        between: between(0, 100)
      },
      pm: {
        required,
        between: between(0, 100)
      }
    },

    methods: {
      calculateResluts(e) {
        this.vacancyAmount = parseFloat(this.rent) * (this.vacancy / 100);
        this.repairAmount = parseFloat(this.rent) * (this.repairs / 100);
        this.propMangAmount = parseFloat(this.rent) * (this.pm / 100);

        this.totalExpenses = parseFloat(this.tax) + parseFloat(this.insurance) + parseFloat(this.vacancyAmount) + parseFloat(this.repairAmount) + parseFloat(this.propMangAmount);

        this.cashFlow = parseFloat(this.rent) - this.totalExpenses;
        this.yearlyCashFlow = this.cashFlow * 12;

        this.cap = ((this.yearlyCashFlow / parseFloat(this.amount)) * 100).toFixed(2) + '%';
      }
    }
  }
</script>

<style scoped>
  .warning, .required {
  color: red;
  font-size: 12px;
}
</style>


