<template>
  <div class="mortgage-calc mb-5">
    <div class="container">
      <div class="row">
        <div class="col-md-6 mx-auto">
          <div class="card card-body text-center mt-5">
            <h1 class="heading display-5 pb-3">Mortgage Loan Calculator</h1>
            <form class="loan-form">
              <p class="required">* All fields are required</p>
              <div class="form-group">
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">$ </div>
                  </div>
                  <input 
                    v-model="amount" 
                    type="number" 
                    class="form-control" 
                    id="amount" 
                    @blur="$v.amount.$touch()" placeholder="Loan Amount">
                    <p v-if="!$v.amount.min" class="warning">Please enter a loan amount greater than 0.</p>
                </div>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <div class="input-group-text">%</div>
                  </div>
                  <input 
                    v-model="interest" 
                    type="number" 
                    class="form-control" 
                    id="interest"
                    @blur="$v.interest.$touch()"
                    step="0.01" 
                    placeholder="Interest">
                  <p v-if="!$v.interest.between" class="warning">Please enter an interest rate between 0 and 100.</p>
                </div>
              </div>
              <div class="form-group">
                <label for="years">Loan Term in Years</label>
                <select
                  v-model="years"
                  class="form-control" 
                  id="years"
                  @blur="$v.years.$touch()" 
                  placeholder="Years to Repay">
                    <option>10</option>
                    <option>15</option>
                    <option>20</option>
                    <option>25</option>
                    <option>30</option>
                    <option>40</option>
                </select>
              </div>
              <div class="form-group">
                <button @click="calculateResluts" class="btn btn-success btn-block" type="button" value="send" :disabled="$v.$invalid">Calculate</button>
              </div>
            </form>

            <!-- Results -->
            <div id="results">
              <h5>Results</h5>
              <div class="form-group">
                <div class="input-group">
                    <div class="input-group-prepend">
                      <div class="input-group-text">Monthly Payment</div>
                    </div>
                  <input v-model="monthlyPayment" type="text" class="form-control" id="monthly-payment" disabled>
                </div>
              </div>
              <div class="form-group">
                <div class="input-group">
                    <div class="input-group-prepend">
                      <div class="input-group-text">Total Payment</div>
                    </div>
                  <input v-model="totalPayment" type="text" class="form-control" id="total-payment" disabled>
                </div>
              </div>
              <div class="form-group">
                <div class="input-group">
                    <div class="input-group-prepend">
                      <div class="input-group-text">Total Interest</div>
                    </div>
                  <input v-model="totalInterest" type="text" class="form-control" id="total-interest" disabled>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  
</template>

<script>
  import {required, minValue, between} from 'vuelidate/lib/validators'

  export default {
    data() {
      return {
        amount: '',
        interest: '',
        years: '',
        monthlyPayment: '',
        totalPayment: '',
        totalInterest: '',
        principal: '',
        calculatedInterest: '',
        calculatedPayments: '',
        x: '',
        monthly: ''
      }
    },

    validations: {
      amount: {
        required,
        min: minValue(1)
      },
      interest: {
        required,
        between: between(0, 100)
      },
      years: {
        required,
        between: between(10,40)
      }
    },

    methods: {
      calculateResluts(e) {
        this.principal = parseFloat(this.amount);
        this.calculatedInterest = parseFloat(this.interest) / 100 / 12;
        this.calculatedPayments = parseFloat(this.years) * 12;
        this.x = Math.pow(1 + this.calculatedInterest, this.calculatedPayments);
        this.monthly = (this.principal*this.x*this.calculatedInterest)/(this.x-1);

        // Calculate Results
        if(isFinite(this.monthly)) {
          this.monthlyPayment = this.monthly.toLocaleString(undefined, {style: "currency", currency: "USD"});
          this.totalPayment = (this.monthly * this.calculatedPayments).toLocaleString(undefined, {style: "currency", currency: "USD"});
          this.totalInterest = ((this.monthly * this.calculatedPayments) - this.principal).toLocaleString(undefined, {style: "currency", currency: "USD"});

          // Show Results
          document.getElementById('results').style.display = 'block';

        }; 
      }
    }
  }
</script>

<style scoped>
#results {
      display: none;
}

.warning, .required {
  color: red;
  font-size: 12px;
}


  
</style>





